

import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { makeStyles } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import SearchIcon from '@material-ui/icons/Search';
import InputBase from '@material-ui/core/InputBase';
import { IconButton } from '@material-ui/core';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  job: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    }
  },
  noJob: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    },
    padding: theme.spacing(2),
  },
  firstRec: {
    marginTop: 0,
  },
  routerLink: {
    flexGrow: 1,
    textDecoration: 'none',
    width: '100%',
    display: 'flex',
    padding: theme.spacing(2),
    color: theme.palette.text.primary,
    '&:visited': {
      color: theme.palette.text.primary,
    },
    '&:active': {
      color: theme.palette.text.primary,
    }
  },
  jobCompany: {
    width: '30%',
    paddingLeft: theme.spacing(.3),
    marginRight: 0,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  jobTitle: {
    width: '70%',
    marginRight: theme.spacing(1),
    paddingLeft: theme.spacing(.3),
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  searchBox: {
    background: theme.palette.background.paper,
    display: 'flex',
    marginBottom: theme.spacing(2),
    cursor: 'pointer',
  },
  input: {
    paddingLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: '43%',
  },
  searchButton: {
    background: theme.palette.secondary.main,
    width: '14%',
    display: 'flex',
    justifyContent: 'center',
    '&:hover': {
      background: theme.palette.secondary.dark,
    },
  },
  searchIcon: {
    background: 'transparent',
    color: theme.palette.background.paper,
    '&:hover': {
      background: 'transparent',
    },
  }

}));

function Jobs() {
  const classes = useStyles();
  const [jobs, setJobs] = useState([]);

  const [searchLocation, setSearchLocation] = useState('');
  const [tag, setTag] = useState('');

  function handleSearch(e) {
    e.preventDefault();
    if (!searchLocation) return;
    axios.get('/job/search', {
      params: {
        location: searchLocation,
        tag: tag ? tag : null,
      },
    })
      .then((res) => {
        if (res.status !== 200) throw Error('something went wrong');
        return res;
      })
      .then((res) => {
        if (res.data.data) setJobs(res.data.data);
      })
      .catch(() => {

      })
  }

  useEffect(() => {
    axios.get('/job')
      .then(res => {
        if (!res.status === 'success') throw Error('something went wrong!');
        return res;
      })
      .then((res) => {
        setJobs(res.data.data);
      })
      .catch(() => {

      })
  }, [setJobs]);

  return (
    <Box>
      <form
        className={classes.searchBox}
        onSubmit={handleSearch}
      >
        <InputBase
          className={classes.input}
          placeholder="enter location"
          id="location"
          key="search-location"
          value={searchLocation}
          onChange={(e) => setSearchLocation(e.target.value)}
        />
        <InputBase
          className={classes.input}
          placeholder="enter tag"
          id="tag"
          key="search-tag"
          value={tag}
          onChange={(e) => setTag(e.target.value)}
        />
        <Box
          className={classes.searchButton}
          onClick={handleSearch}
        >
          <IconButton
            type="submit"
            className={classes.searchIcon}
            disableRipple
          >
            <SearchIcon />
          </IconButton>
        </Box>
      </form>

      {(jobs.length) ? (
        jobs.map(({ jid, title, companyName }, index) => {
          return (
            <Paper
              key={jid + ' ' + title}
              className={classes.job + ' ' + (index === 0 ? classes.firstRec : '')}
            >
              <RouterLink to={`/job/${jid}`} className={classes.routerLink}>
                <Box className={classes.jobTitle}>
                  {title}
                </Box>
                <Box className={classes.jobCompany}>
                  {companyName}
                </Box>
              </RouterLink>
            </Paper>
          );
        })
      ) : (
          <Paper
            className={classes.noJob}
          >
            No matching jobs!
            </Paper>
        )}
    </Box>
  );
}

export default Jobs;