

import React, { useEffect, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { makeStyles } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  job: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    }
  },
  firstRec: {
    marginTop: 0,
  },
  routerLink: {
    flexGrow: 1,
    textDecoration: 'none',
    width: '100%',
    display: 'flex',
    padding: theme.spacing(2),
    color: theme.palette.text.primary,
    '&:visited': {
      color: theme.palette.text.primary,
    },
    '&:active': {
      color: theme.palette.text.primary,
    }
  },
  jobCompany: {
    width: '30%',
    paddingLeft: theme.spacing(.3),
    marginRight: 0,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  jobTitle: {
    width: '70%',
    marginRight: theme.spacing(1),
    paddingLeft: theme.spacing(.3),
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  noJob: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    },
    padding: theme.spacing(2),
  },
}));

function AppliedJobs() {
  const classes = useStyles();

  const [jobs, setJobs] = useState([]);

  useEffect(() => {
    axios.get('/job/applied')
      .then((res) => {
        if (res.status !== 200) throw Error('Something went wrong');
        return res;
      })
      .then((res) => {
        setJobs(res.data.data);
      })
      .catch(() => {

      })
  }, [setJobs]);

  return (
    <>
      {(jobs.length) ? (
        jobs.map(({ jid, title, description }, index) => {
          return (
            <Paper
              key={jid + ' ' + title}
              className={classes.job + ' ' + (index === 0 ? classes.firstRec : '')}
            >
              <RouterLink to={`/job/${jid}`} className={classes.routerLink}>
                <Box className={classes.jobTitle}>
                  {title}
                </Box>
                <Box className={classes.jobCompany}>
                  {description}
                </Box>
              </RouterLink>
            </Paper>
          );
        })
      ) : (
          <Paper
            className={classes.noJob}
          >
            You have not applied  for any job yet!
            </Paper>
        )}
    </>
  );
}

export default AppliedJobs;