import React, { useEffect, useState } from 'react';
import { useParams, Link as RouterLink } from 'react-router-dom';
import { Box, makeStyles, Container } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(12),
  },
  selfDetails: {
    padding: theme.spacing(3, 2),
  },
  typographyBigFont: {
    fontSize: '1.5rem',
  },
  applicants: {
    // padding: theme.spacing(3, 2),
  },
  applicantsHeader: {
    marginTop: theme.spacing(1.5),
    padding: theme.spacing(2),
  },
  applicantsContainer: {
    marginTop: theme.spacing(.5),
  },

  applicant: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    },
    // padding: theme.spacing(2),
  },
  routerLink: {
    flexGrow: 1,
    textDecoration: 'none',
    width: '100%',
    display: 'flex',
    padding: theme.spacing(2),
    color: theme.palette.text.primary,
    '&:visited': {
      color: theme.palette.text.primary,
    },
    '&:active': {
      color: theme.palette.text.primary,
    }
  },
  applicantName: {
    width: '33%',
    marginRight: theme.spacing(1),
    paddingLeft: theme.spacing(.3),
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  applicantEdtn: {
    width: '33%',
    marginRight: theme.spacing(1),
    paddingLeft: theme.spacing(.3),
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  applicantExp: {
    width: '33%',
    paddingLeft: theme.spacing(.3),
    marginRight: 0,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },

  noApplicant: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    },
    padding: theme.spacing(2),
  },
}));

function JobDetails({ user, recruiter }) {
  const params = useParams();
  const classes = useStyles();

  const { id } = params;
  const [job, setJob] = useState({});

  useEffect(() => {
    axios.get(`/recruiter/job/${id}`)
      .then((res) => {
        if (res.status !== 200) throw Error('something went wrong');
        return res;
      })
      .then((res) => {
        if (res.data.data) {
          setJob(res.data.data);
        }
      })
      .catch(() => {

      })
  }, [setJob, id]);

  return (
    <Container className={classes.root}>
      <Paper className={classes.selfDetails}>
        <Typography
          variant="body1"
          className={classes.typographyBigFont}
        >
          Title: {job.title}
        </Typography>
        <Typography
          variant="body1"
        >
          Description: {job.description}
        </Typography>
        <Typography
          variant="body1"
        >
          Location: {job.location}
        </Typography>
        <Typography
          variant="body1"
        >
          Tag: {job.tag}
        </Typography>
      </Paper>
      <Box className={classes.applicants}>
        <Typography
          variant="h3"
          className={classes.typographyBigFont + ' ' + classes.applicantsHeader}
        >
          Applicants:
        </Typography>
        <Box className={classes.applicantsContainer}>
          {(job && job.applicants && job.applicants.length) ? (
            job.applicants.map((applicant) => {
              return (
                <Paper
                  key={'applicant' + applicant.id + ' ' + applicant.name}
                  className={classes.applicant}
                >
                  <RouterLink to={`/applicant/${applicant.id}`} className={classes.routerLink}>
                    <Box className={classes.applicantName}>
                      {applicant.name}
                    </Box>
                    <Box className={classes.applicantEdtn}>
                      {applicant.highestEducation}
                    </Box>
                    <Box className={classes.applicantExp}>
                      {applicant.experience} years of experience
                  </Box>
                  </RouterLink>
                </Paper>
              );
            })
          ) : (
              <Paper
                className={classes.noApplicant}
              >
                Looks like no one has applied yet!
              </Paper>
            )}
        </Box>
      </Box>
    </Container>
  );
}

export default JobDetails;