import React, { useState, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { Box, makeStyles, Container } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(12),
  },
  selfDetails: {
    padding: theme.spacing(3, 2),
  },
  typographyBigFont: {
    margin: theme.spacing(1, 0),
    fontSize: '1.5rem',
  },
  applicants: {
    // padding: theme.spacing(3, 2),
  },
  applicantsHeader: {
    marginTop: theme.spacing(3),
    marginBottom: 0,
  },
  applicantsContainer: {
    // marginTop: theme.spacing(),
  },

  formControl: {
    width: '100%',
    margin: theme.spacing(2, 0),
  },
  doneButton: {
    margin: theme.spacing(4, 0, 2, 0),
  }
}));

function Applicant() {
  const params = useParams();
  const history = useHistory();

  const [applicationStatus, setApplcationStatus] = useState('accepted');
  const [rejectionMessage, setRejectionMessage] = useState('');
  const [interviewLetter, setInterviewLetter] = useState('');
  const [applicant, setApplicant] = useState({});
  const classes = useStyles();

  const { id } = params;

  useEffect(() => {
    axios.get(`/recruiter/applicant/${id}`)
      .then((res) => {
        if (res.status !== 200) throw Error('Something went wrong');
        return res;
      })
      .then((res) => {
        setApplicant(res.data.data);
      })
      .catch(() => {

      })
  }, [id, setApplicant]);


  function handleApplicationUpdate() {
    if (!(interviewLetter || rejectionMessage)) return;
    const updatedStatus = {};
    if (applicationStatus === 'accepted') {
      updatedStatus.status = 1;
      updatedStatus.message = interviewLetter;
    } else {
      updatedStatus.status = 2;
      updatedStatus.message = rejectionMessage;
    }

    axios.put('/recruiter/applicant', {
      id: applicant.id,
      status: updatedStatus.status,
      message: updatedStatus.message
    })
      .then((res) => {
        console.log(res);
        if (res.status !== 200) throw Error('Something went wrong');
        history.goBack();
      })
      .catch(() => {

      })
  }


  return (
    <Container className={classes.root}>
      <Paper className={classes.selfDetails}>
        <Typography
          variant="body1"
          className={classes.typographyBigFont}
        >
          Applicant details
        </Typography>
        <Typography
          variant="body1"
        >
          Name: {applicant.name}
        </Typography>
        <Typography
          variant="body1"
        >
          Highest Education: {applicant.highestEducation}
        </Typography>
        <Typography
          variant="body1"
        >
          Experience: {applicant.experience} years
        </Typography>
        <Typography
          variant="body1"
        >
          Current Application status: {
            applicant.status === 0 ? 'New' : (applicant.status === 1) ? 'Active' : 'Rejected'
          }
        </Typography>

        <Box className={classes.applicants}>
          <Box className={classes.action}>
            <Typography
              variant="h3"
              className={classes.typographyBigFont + ' ' + classes.applicantsHeader}
            >
              Update application status:
            </Typography>

            <FormControl component="fieldset" className={classes.formControl}>
              <RadioGroup
                aria-label="reject or accept the application"
                name="application status"
                value={applicationStatus}
                onChange={(e) => setApplcationStatus(e.target.value)}
                row
              >
                <FormControlLabel value="accepted" control={<Radio />} label="Accept" />
                <FormControlLabel value="rejected" control={<Radio />} label="Reject" />
              </RadioGroup>
            </FormControl>
            {(applicationStatus === 'rejected') ? (
              <TextField
                name="application-status-reject"
                value={rejectionMessage}
                onChange={(e) => setRejectionMessage(e.target.value)}
                variant="outlined"
                fullWidth
                multiline
                rows={5}
                id="rejectionMessage"
                label="Write a rejection message"
                placeholder="Write a rejection message"
              />
            ) : (
                <TextField
                  name="application-status-accept"
                  value={interviewLetter}
                  onChange={(e) => setInterviewLetter(e.target.value)}
                  variant="outlined"
                  multiline
                  fullWidth
                  rows={5}
                  id="interviewLetter"
                  label="Write an interview letter"
                  placeholder="Write an interview letter"
                />
              )}
            <Button
              color="primary"
              type="button"
              fullWidth
              variant="contained"
              className={classes.doneButton}
              onClick={handleApplicationUpdate}
            >
              Done
            </Button>
          </Box>

        </Box>
      </Paper>
    </Container>
  );
}

export default Applicant;