import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { Paper, Typography, makeStyles, } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(12),
  },
  jobsContainer: {
    marginTop: theme.spacing(3),
  },
  jobCard: {
    padding: theme.spacing(3, 2),
    marginBottom: theme.spacing(2),
    '&:hover': {
      boxShadow: theme.shadows[3],
    }
  },
  fab: {
    margin: theme.spacing(1),
    position: 'fixed',
    bottom: '4vh',
    right: '4vw',
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    width: '60%',
    background: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    '&:focus': {
      outline: 'none',
    },
  },
  form: {
    width: '65%',
    height: '80vh',
    // padding: theme.spacing(4, 1),
    margin: '0 auto',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  formHeader: {
    marginBottom: theme.spacing(5),
  },
  addButton: {
    margin: theme.spacing(0, 1),
    marginTop: theme.spacing(2),
  }
}));

function RecruiterHome({ user, recruiter }) {

  const [jobs, setJobs] = useState([]);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    axios.get('/recruiter/job')
      .then(res => {
        if (res.status !== 200) throw Error('something went wrong');
        return res;
      })
      .then(res => {
        setJobs(res.data.data);
      })
  }, [setJobs]);

  function handleOpen() {
    setOpen(true);
  };

  function handleClose() {
    setOpen(false);
  };

  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [location, setLocation] = useState('');
  const [tag, setTag] = useState('');

  function handleSubmit(e) {
    e.preventDefault();
    if ([title, description, location, tag].every(e => !!e)) {
      // do the call here
      axios.post('/recruiter/job/new', {
        title,
        description,
        location,
        tag,
      })
        .then((res) => {
          if (res.status !== 200) throw Error('something went wrong');
          return res;
        })
        .then((res) => {
          setJobs([...jobs, {
            jid: res.data.data.jid,
            title,
            description,
            location,
            tag
          }]);
          setOpen(false);
          setTitle('');
          setDescription('');
          setLocation('');
          setTag('');
        })
        .catch(() => {

        })
    }
  }

  const classes = useStyles();

  return (
    <Container className={classes.root}>
      <Typography
        variant="h4"
        component="h1"
      >
        Jobs added by you
      </Typography>
      <Box className={classes.jobsContainer}>
        {jobs.map((job) => {
          return (
            <RouterLink
              to={`/job/${job.jid}`}
              style={{
                textDecoration: 'none'
              }}
              key={`${job.jid} ${job.title}`}
            >
              <Paper className={classes.jobCard}>
                <Typography variant="h5" component="h3">
                  {job.title.slice(0, 60) + (
                    job.description.length > 60 ? '...' : ''
                  )}
                </Typography>
                <Typography component="p">
                  {job.description.slice(0, 60) + (
                    job.description.length > 60 ? '...' : ''
                  )}
                </Typography>
              </Paper>
            </RouterLink>
          );
        })}
      </Box>
      <Fab
        color="secondary"
        aria-label="add a job, opens new popup"
        className={classes.fab}
        onClick={handleOpen}
      >
        <AddIcon />
      </Fab>

      <Modal
        aria-label="job creation popup"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <Box
            className={classes.paper}
          >
            <form
              id="signupForm"
              className={classes.form}
              noValidate
              onSubmit={handleSubmit}
            >
              <Typography
                variant="h4"
                component="h2"
                className={classes.formHeader}
              >
                Add a job
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    id="title"
                    label="title"
                    name="title"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    multiline
                    name="description"
                    label="description"
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    name="location"
                    label="location"
                    id="location"
                    value={location}
                    onChange={(e) => setLocation(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    name="tag"
                    label="tag/keyword"
                    placeholder="tag/keyword to find your job"
                    id="tag"
                    value={tag}
                    onChange={(e) => setTag(e.target.value)}
                  />
                </Grid>
                <Button
                  color="primary"
                  type="submit"
                  fullWidth
                  variant="contained"
                  className={classes.addButton}
                >
                  Add
                </Button>
              </Grid>
            </form>
          </Box>
        </Fade>
      </Modal>
    </Container >
  );
}

export default RecruiterHome;