import React, { useState, useEffect } from 'react';
import { Box, makeStyles, Container } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
// import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { useParams } from 'react-router-dom';
import axios from 'axios';

// status -- 0 - no action yet, 1 - accepted, 2 - rejected/suspended

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(12),
    width: '80%',
  },
  selfDetails: {
    padding: theme.spacing(3, 2),
  },
  typographyBigFont: {
    margin: theme.spacing(1, 0),
    fontSize: '1.5rem',
  },
  recruiters: {
    // padding: theme.spacing(3, 2),
  },
  recruitersHeader: {
    margin: theme.spacing(3, 0),
    // marginBottom: 0,
  },
  recruitersContainer: {
    // marginTop: theme.spacing(),
  },

  formControl: {
    width: '100%',
    margin: theme.spacing(0, 0, 0, 2),
  },
  doneButton: {
    margin: theme.spacing(4, 0, 2, 0),
  }
}));

function Recruiter() {

  const [applicationStatus, setApplcationStatus] = useState('new');
  const [newStatusRadioValue, setNewStatusRadioValue] = useState('accepted');
  // const [welcomeMessage, setWelcomeMessage] = useState('');
  // const [suspensionMessage, setSuspensionMessage] = useState('');
  // const [welcomeAgainMessage, setWelcomeAgainMessage] = useState('');
  const params = useParams();

  const { id } = params;

  const classes = useStyles();

  const [isLoading, setIsLoading] = useState(true);
  const [recruiter, setRecruiter] = useState({});

  const [newRecruiterStatus, setNewRecruiterStatus] = useState('');
  useEffect(() => {

    axios.get(`/admin/recruiter/${id}`)
      .then((res) => {

        setRecruiter(res.data.data);
        setIsLoading(false);
      })
      .catch((e) => {
        setIsLoading(false);
      })

  }, [setRecruiter, applicationStatus, id,]);


  useEffect(() => {
    if (!isLoading) {
      // console.log('recruiter status ', recruiter);
      if (recruiter.status === 0) {
        setApplcationStatus('New');
        setNewRecruiterStatus('Accept');
        setNewStatusRadioValue('accepted')
      } else if (recruiter.status === 1) {
        setApplcationStatus('Active');
        setNewRecruiterStatus('Suspend');
      } else {
        setApplcationStatus('Reject');
        setNewRecruiterStatus('Activate');
      }
    }
  }, [isLoading, recruiter.status,
    setApplcationStatus,
    setNewRecruiterStatus]);


  function handleSubmit() {
    let newStatus;
    if (applicationStatus === 'New') {
      newStatus = newStatusRadioValue === 'accepted' ? 1 : 2;
    } else if (newRecruiterStatus === 'Suspend') {
      newStatus = 2;
    } else {
      newStatus = 1;
    }

    axios.put(`/admin/recruiter/${id}`, {
      uid: recruiter.uid, newStatus,
    })
      .then((res) => {
        if (res.status !== 200) {
          throw Error('Could not update status')
        }
        return res.data.status;
      })
      .then((success) => {
        if (success) {
          console.log('success ');
          setRecruiter({ ...recruiter, status: newStatus });
        }
      })
      .catch((error) => {
        // handle error here
      })
  }


  return (
    <Container className={classes.root}>

      {(!isLoading) && (
        <Paper className={classes.selfDetails}>
          <Typography
            variant="body1"
            className={classes.typographyBigFont}
          >
            Recruiter details
        </Typography>
          <Typography
            variant="body1"
          >
            Name: {recruiter.name}
          </Typography>
          <Typography
            variant="body1"
          >
            Company Name: {recruiter.company}
          </Typography>
          <Typography
            variant="body1"
          >
            Position: {recruiter.position}
          </Typography>
          <Typography
            variant="body1"
          >
            Location: {recruiter.address}
          </Typography>

          <Typography
            variant="body1"
          >
            Account Status: {applicationStatus}
          </Typography>

          <Box className={classes.recruiters}>
            <Box className={classes.action}>
              <Typography
                variant="h3"
                className={classes.typographyBigFont + ' ' + classes.recruitersHeader}
              >
                {newRecruiterStatus} Recruiter account
              </Typography>

              {(recruiter.status === 0) && (
                <FormControl component="fieldset" className={classes.formControl}>
                  <RadioGroup
                    aria-label="reject or accept the application"
                    name="application status"
                    value={newStatusRadioValue}
                    onChange={(e) => setNewStatusRadioValue(e.target.value)}
                    row
                  >
                    <FormControlLabel value="accepted" control={<Radio />} label="Accept" />
                    <FormControlLabel value="rejected" control={<Radio />} label="Reject" />
                  </RadioGroup>
                </FormControl>
              )}
              {/* {(applicationStatus === 'rejected') ? (
                    <TextField
                      name="application-status-reject"
                      value={rejectionMessage}
                      onChange={(e) => setRejectionMessage(e.target.value)}
                      variant="outlined"
                      fullWidth
                      multiline
                      rows={5}
                      id="rejectionMessage"
                      label="Write a rejection message"
                      placeholder="Write a rejection message"
                    />
                  ) : (
                      <TextField
                        name="application-status-accept"
                        value={welcomeMessage}
                        onChange={(e) => setWelcomeMessage(e.target.value)}
                        variant="outlined"
                        multiline
                        fullWidth
                        rows={5}
                        id="welcomeMessage"
                        label="Write a welcome message"
                        placeholder="Write a welcome message"
                      />
                    )} */}

              {/* {(recruiter.status === 'active') && (
                <TextField
                  name="application-status-accept"
                  value={suspensionMessage}
                  onChange={(e) => setSuspensionMessage(e.target.value)}
                  variant="outlined"
                  multiline
                  fullWidth
                  rows={5}
                  id="suspensionMessage"
                  label="Write the reason for suspension"
                  placeholder="Write the reason suspension"
                />
              )}

              {(recruiter.status === 'suspended') && (
                <TextField
                  name="application-status-accept"
                  value={welcomeAgainMessage}
                  onChange={(e) => setWelcomeAgainMessage(e.target.value)}
                  variant="outlined"
                  multiline
                  fullWidth
                  rows={5}
                  id="welcomeAgainMessage"
                  label="Welcome the recruiter again"
                  placeholder="Welcome the recruiter again"
                />
              )} */}

              <Button
                color="primary"
                type="button"
                fullWidth
                variant="contained"
                className={classes.doneButton}
                onClick={handleSubmit}
              >
                {newRecruiterStatus}
              </Button>
            </Box>

          </Box>
        </Paper>
      )}

    </Container>
  );
}

export default Recruiter;