

import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { makeStyles } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  recruiter: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    }
  },
  firstRec: {
    marginTop: 0,
  },
  routerLink: {
    flexGrow: 1,
    textDecoration: 'none',
    width: '100%',
    display: 'flex',
    padding: theme.spacing(2),
    color: theme.palette.text.primary,
    '&:visited': {
      color: theme.palette.text.primary,
    },
    '&:active': {
      color: theme.palette.text.primary,
    }
  },
  recruiterName: {
    width: '50%',
    marginRight: theme.spacing(1),
    paddingLeft: theme.spacing(.3),
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  recruiterCompany: {
    width: '50%',
    paddingLeft: theme.spacing(.3),
    marginRight: 0,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },

  EmptyRecruiter: {
    padding: theme.spacing(2),
  },
}));

function Recruiters({ recruiterType }) {
  const classes = useStyles();

  const [recruiters, setRecruiters] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (recruiterType === 'new') {
      axios.get('/admin/recruiter/new').then(res => {
        setRecruiters(res.data.data);
        setIsLoading(false);
      });
    } else {
      axios.get('/admin/recruiter').then(res => {
        setRecruiters(res.data.data);
        setIsLoading(false);
      });
    }

  }, [setRecruiters, setIsLoading, recruiterType]);

  return (
    <>
      {(!isLoading) && (
        (recruiters.length > 0) ? (
          recruiters.map(({ uid, name, company }, index) => {
            return (
              <Paper
                key={uid + ' ' + name}
                className={classes.recruiter + ' ' + (index === 0 ? classes.firstRec : '')}
              >
                <RouterLink to={`/recruiter/${uid}`} className={classes.routerLink}>
                  <Box className={classes.recruiterName}>
                    {name}
                  </Box>
                  <Box className={classes.recruiterCompany}>
                    {company}
                  </Box>
                </RouterLink>
              </Paper>
            );
          })
        ) : (
            <>
              {(recruiterType === 'new') ? (
                <Paper
                  className={classes.EmptyRecruiter}
                >
                  No new Recruiter has applied!
                </Paper>
              ) : (
                  <Paper
                    className={classes.EmptyRecruiter}
                  >
                    Looks like we don't have any recruiters!
                  </Paper>
                )}
            </>
          )
      )}
    </>
  );
}

export default Recruiters;