import React, { useState, useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';

import JobDetails from './JobDetails';
import RecruiterHome from './RecruiterHome';
import Applicant from './Applicant';
import { Box, makeStyles } from '@material-ui/core';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  topMargin: {
    marginTop: theme.spacing(10),
    fontSize: '2em',
    padding: theme.spacing(4, 6),
  },
}));

function Recruiter({ user }) {

  const [isRecruiterLoading, setIsRecruiterLoading] = useState(true);
  const [recruiter, setRecruiter] = useState({});

  useEffect(() => {
    axios.get('/recruiter')
      .then((res) => {
        setRecruiter({ ...res.data.data });
        setIsRecruiterLoading(false);
      })
      .catch(() => {
        setIsRecruiterLoading(false);
      })
  }, [setIsRecruiterLoading, setRecruiter]);

  const classes = useStyles();

  return (
    <>
      {(!isRecruiterLoading) && (
        <>
          {(recruiter.status && recruiter.status === 1) ? (
            <Switch>
              <Route exact path="/job/:id" render={(props) => <JobDetails {...props} user={user} recruiter={recruiter} />} />
              <Route exact path="/applicant/:id" render={(props) => <Applicant {...props} user={user} recruiter={recruiter} />} />
              <Route render={(props) => <RecruiterHome {...props} user={user} recruiter={recruiter} />} />
            </Switch>
          ) : (
              <Box className={classes.topMargin}>
                Your Recruiter account is not active.
                 If you have registered recently, We will be verifying your
                 account within 12 hours. You can always contact our customer
                 service to clear your doubts.
              </Box>
            )}
        </>

      )}

    </>
  );
}

export default Recruiter;