

import React, { useEffect, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { makeStyles } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  interview: {
    marginTop: theme.spacing(1.5),
    display: 'flex',
    boxShadow: theme.shadows[1],
    '&:hover': {
      boxShadow: theme.shadows[3],
    }
  },
  firstRec: {
    marginTop: 0,
  },
  routerLink: {
    flexGrow: 1,
    textDecoration: 'none',
    width: '100%',
    display: 'flex',
    padding: theme.spacing(2),
    color: theme.palette.text.primary,
    '&:visited': {
      color: theme.palette.text.primary,
    },
    '&:active': {
      color: theme.palette.text.primary,
    }
  },
  interviewCompany: {
    width: '70%',
    paddingLeft: theme.spacing(.3),
    marginRight: 0,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  interviewTitle: {
    width: '30%',
    marginRight: theme.spacing(1),
    paddingLeft: theme.spacing(.3),
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
}));

function Interviews() {
  const classes = useStyles();

  const [interviews, setInterviews] = useState([]);

  useEffect(() => {
    axios.get('/application/interviews')
      .then((res) => {
        if (res.status !== 200) throw Error('something went wrong');
        return res;
      })
      .then((res) => {
        setInterviews(res.data.data);
      })
      .catch(() => {

      });
  }, [setInterviews]);



  return (
    <>
      {(interviews.length) ? (
        interviews.map(({ id, title, message }, index) => {
          return (
            <Paper
              key={'interview' + id + ' ' + title}
              className={classes.interview + ' ' + (index === 0 ? classes.firstRec : '')}
            >
              <RouterLink to={`/interview/${id}`} className={classes.routerLink}>
                <Box className={classes.interviewTitle}>
                  {title}
                </Box>
                <Box className={classes.interviewCompany}>
                  {message.slice(0, 40) + (message.length > 40 ? '...' : '')}
                </Box>

              </RouterLink>
            </Paper>
          );
        })
      ) : (
          <Paper
            className={classes.interview}
          >
            You have not recieved any interview invite yet!
            </Paper>
        )}
    </>
  );
}

export default Interviews;